import React from 'react';
import './styles.css';

const types = {
  'assigned': '',
  'reopen': '',
  'reject': '',
  'deal': '',
  'note': '',
  'created': '',
  'task': ''
}

const getCommentText = ({ type, comment, amount, user, trunk, reason, task }) => {
  if (type === 'task') return '  ' + comment + '.   ' + task.when + '  ' + task.time
  if (type === 'assigned') return '   '
  if (type === 'reopen') return '  '
  if (type === 'reject') return '  .   ' + (comment ? comment : reason)
  if (type === 'deal') return ' .   ' + amount + '.' + (comment ? comment : '')
  if (type === 'note') return comment
  if (type === 'created') {
    return '  ' + (user ?
      '' : '     ' + trunk.name + ''
    )
  }
}

const Label = ({ date, type, user }) =>
  <div className="bCustomerBreadcrumb__label">
    {date}&nbsp;
    <span className="bCustomerBreadcrumb__divider"></span>&nbsp;
    {user ? user.name : ''}&nbsp;&nbsp;{types[type]}
  </div>;

const Comment = params =>
  <div className="bCustomerBreadcrumb__comment">
    {getCommentText(params)}
  </div>;

export default ({ type, date, comment, amount, reason, user, trunk, previousStep, task }) =>
  <div className="bCustomerBreadcrumb">
    <Label type={type} date={date} user={user} />
    <Comment type={type} user={user} comment={comment}
      amount={amount} trunk={trunk} reason={reason} task={task} />
  </div>;



